<template>
  <div class="scene">
    <div id="container" ref="three-scene-canvas"></div>
  </div>
</template>

<script>
import * as THREE from "three";
import Stats from "three/examples/jsm/libs/stats.module.js";
import { GUI } from "three/examples/jsm/libs/dat.gui.module.js";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { SVGLoader } from "three/examples/jsm/loaders/SVGLoader.js";

export default {
  name: "Cube",
  data() {
    return {
      container: null,
      camera: null,
      scene: null,
      stats: null,
      bulbLight: null,
      bulbMat: null,
      hemiLight: null,
      ballMat: { needsUpdate: false },
      cubeMat: { needsUpdate: false },
      floorMat: { needsUpdate: false },

      textureLoader: {},

      previousShadowMap: false,

      bulbLuminousPowers: {
        "110000 lm (1000W)": 110000,
        "3500 lm (300W)": 3500,
        "1700 lm (100W)": 1700,
        "800 lm (60W)": 800,
        "400 lm (40W)": 400,
        "180 lm (25W)": 180,
        "20 lm (4W)": 20,
        Off: 0,
      },
      hemiLuminousIrradiances: {
        "0.0001 lx (Moonless Night)": 0.0001,
        "0.002 lx (Night Airglow)": 0.002,
        "0.5 lx (Full Moon)": 0.5,
        "3.4 lx (City Twilight)": 3.4,
        "50 lx (Living Room)": 50,
        "100 lx (Very Overcast)": 100,
        "350 lx (Office Room)": 350,
        "400 lx (Sunrise/Sunset)": 400,
        "1000 lx (Overcast)": 1000,
        "18000 lx (Daylight)": 18000,
        "50000 lx (Direct Sun)": 50000,
      },
      params: {},
    };
  },
  renderer: null,
  watch: {},
  mounted() {
    let obj_canvas = JSON.parse(localStorage.getItem("obj_canvas"));
    let obj_svg = JSON.parse(localStorage.getItem("svg_obj"));
    let background_details = JSON.parse(
      localStorage.getItem("backgroud_details")
    );
    this.composeDocument(obj_canvas, background_details, obj_svg);
    this.animate();
  },
  methods: {
    composeDocument(p_obj_canvas, p_background_details, obj_svg) {
      /* **************
       BASIC SETUP
    ************** */

      this.params = {
        shadows: true,
        exposure: 0.8,
        bulbPower: Object.keys(this.bulbLuminousPowers)[4],
        hemiIrradiance: Object.keys(this.hemiLuminousIrradiances)[3],
      };

      this.container = this.$refs["three-scene-canvas"];
      this.stats = new Stats();
      //this.container.appendChild(this.stats.dom);

      this.camera = new THREE.PerspectiveCamera(
        50,
        window.innerWidth / window.innerHeight,
        0.1,
        80
      );
      this.camera.position.x = 0;
      this.camera.position.z = 10;
      this.camera.position.y = 18;

      this.scene = new THREE.Scene();
      this.scene.background = new THREE.Color(0xcce0ff);

      const bulbGeometry = new THREE.SphereGeometry(0.0, 16, 8);
      this.bulbLight = new THREE.PointLight(0xffee88, 1, 100, 2);

      this.bulbMat = new THREE.MeshStandardMaterial({
        emissive: 0xffffee,
        emissiveIntensity: 1,
        color: 0x000000,
      });
      this.bulbLight.add(new THREE.Mesh(bulbGeometry, this.bulbMat));
      this.bulbLight.position.set(0, 2, 0);
      this.bulbLight.castShadow = true;
      this.scene.add(this.bulbLight);

      this.hemiLight = new THREE.HemisphereLight(0xddeeff, 0x0f0e0d, 0.02);
      this.scene.add(this.hemiLight);

      /***texture floor */
      this.textureLoader = new THREE.TextureLoader();
      var that = this;
      this.floorMat = new THREE.MeshStandardMaterial({
        roughness: 0.8,
        color: 0xffffff,
        metalness: 0.2,
        bumpScale: 0.0005,
      });

      var source_texture = "textures/" + p_background_details.texture + ".png";
      if (p_background_details.texture != "retina_wood") {
        this.textureLoader.load(source_texture, function(map) {
          map.wrapS = THREE.RepeatWrapping;
          map.wrapT = THREE.RepeatWrapping;
          map.anisotropy = 16;
          map.repeat.set(0.008, 0.008);
          map.encoding = THREE.sRGBEncoding;
          that.floorMat.map = map;
          that.floorMat.needsUpdate = true;
        });
      } else {
        this.textureLoader.load(source_texture, function(map) {
          map.wrapS = THREE.RepeatWrapping;
          map.wrapT = THREE.RepeatWrapping;
          map.anisotropy = 4;
          map.repeat.set(0.008, 0.008);
          map.encoding = THREE.sRGBEncoding;
          that.floorMat.map = map;
          that.floorMat.needsUpdate = true;
        });
        this.textureLoader.load("textures/hardwood2_bump.jpg", function(map) {
          map.wrapS = THREE.RepeatWrapping;
          map.wrapT = THREE.RepeatWrapping;
          map.anisotropy = 4;
          map.repeat.set(0.008, 0.008);
          that.floorMat.bumpMap = map;
          that.floorMat.needsUpdate = true;
        });
        this.textureLoader.load("textures/hardwood2_roughness.jpg", function(
          map
        ) {
          map.wrapS = THREE.RepeatWrapping;
          map.wrapT = THREE.RepeatWrapping;
          map.anisotropy = 4;
          map.repeat.set(0.008, 0.008);
          that.floorMat.roughnessMap = map;
          that.floorMat.needsUpdate = true;
        });
      }

      //plane
      const data = new SVGLoader().parse(obj_svg[0]);
      var paths = data.paths;
      var shapes = [];

      for (var i = 0; i < paths.length; i++) {
        Array.prototype.push.apply(shapes, paths[i].toShapes()); // catenate, so we can create a single geometry and mesh
      }

      var extrusionSettings = {
        depth: 2,
        bevelEnabled: false,
        amount: 0.2,
        bevelEnabled: false,
        material: 5,
        extrudeMaterial: 1,
      };

      var geometry = new THREE.ExtrudeBufferGeometry(shapes, extrusionSettings);
      var floorMesh = new THREE.Mesh(geometry, that.floorMat);

      floorMesh.receiveShadow = true;
      floorMesh.rotation.x = Math.PI / 2;
      floorMesh.rotation.z = -Math.PI * 2;
      floorMesh.scale.x = 0.01; // SCALE
      floorMesh.scale.y = 0.01; // SCALE
      floorMesh.scale.z = 0.01; // SCALE
      floorMesh.position.x = -6;
      floorMesh.position.z = -5.3;
      this.scene.add(floorMesh);

      //vasos
      p_obj_canvas.forEach((objVaso2d, i) => {
        if (objVaso2d.type != "group") return false;
        let p_width = objVaso2d.width / 100;
        let p_height = objVaso2d.height / 100;
        let p_left = objVaso2d.left / 100 - 6;
        let p_angle = objVaso2d.angle * -1;
        let radianos = 2 * Math.PI * (p_angle / 360);
        let p_z = objVaso2d.top / 100 - 5.28;

        var tmpCubeMat = new THREE.MeshStandardMaterial({
          roughness: 0.7,
          bumpScale: 0.002,
          metalness: 0.2,
        });
        var texture_vaso = objVaso2d.objects[0].fill.substring(
          1,
          objVaso2d.objects[0].fill.lengh
        );
        this.textureLoader.load("textures/" + texture_vaso + ".jpeg", function(
          map
        ) {
          map.wrapS = THREE.RepeatWrapping;
          map.wrapT = THREE.RepeatWrapping;
          map.anisotropy = 4;
          map.repeat.set(1, 1);
          map.encoding = THREE.sRGBEncoding;
          tmpCubeMat.map = map;
          tmpCubeMat.bumpMap = map;
          tmpCubeMat.needsUpdate = true;
        });

        var boxGeometry = new THREE.BoxGeometry(p_width, 0.5, p_height);
        var boxMesh = new THREE.Mesh(boxGeometry, tmpCubeMat);

        boxMesh.rotation.set(0, radianos, 0);
        boxMesh.position.set(p_left, 0.25, p_z);

        boxMesh.castShadow = true;
        this.scene.add(boxMesh);
      });
      /*** end objects */

      this.renderer = new THREE.WebGLRenderer();
      this.renderer.physicallyCorrectLights = true;
      this.renderer.outputEncoding = THREE.sRGBEncoding;
      this.renderer.shadowMap.enabled = true;
      this.renderer.toneMapping = THREE.ReinhardToneMapping;
      this.renderer.setPixelRatio(window.devicePixelRatio);
      this.renderer.setSize(
        this.container.offsetWidth,
        this.container.offsetHeight
      );
      this.container.append(this.renderer.domElement);

      this.renderer = new THREE.WebGLRenderer();
      this.renderer.physicallyCorrectLights = true;
      this.renderer.outputEncoding = THREE.sRGBEncoding;
      this.renderer.shadowMap.enabled = true;
      this.renderer.toneMapping = THREE.ReinhardToneMapping;
      this.renderer.setPixelRatio(window.devicePixelRatio);
      this.renderer.setSize(window.innerWidth, window.innerHeight);
      this.container.appendChild(this.renderer.domElement);

      const controls = new OrbitControls(this.camera, this.renderer.domElement);
      controls.minDistance = 1;
      controls.maxDistance = 20;
      controls.maxPolarAngle = (0.99 * Math.PI) / 2;

      window.addEventListener("resize", () => {
        this.onWindowResize();
      });
    },
    onWindowResize() {
      this.camera.aspect = window.innerWidth / window.innerHeight;
      this.camera.updateProjectionMatrix();

      this.renderer.setSize(window.innerWidth, window.innerHeight);
    },

    animate() {
      requestAnimationFrame(this.animate);

      this.render();
    },
    render() {
      this.renderer.toneMappingExposure = Math.pow(this.params.exposure, 5.0); // to allow for very bright scenes.
      this.renderer.shadowMap.enabled = this.params.shadows;
      this.bulbLight.castShadow = this.params.shadows;

      if (this.params.shadows !== this.previousShadowMap) {
        this.ballMat.needsUpdate = true;
        this.cubeMat.needsUpdate = true;
        this.floorMat.needsUpdate = true;
        this.previousShadowMap = this.params.shadows;
      }

      this.bulbLight.power = this.bulbLuminousPowers[this.params.bulbPower];
      this.bulbMat.emissiveIntensity =
        this.bulbLight.intensity / Math.pow(0.02, 2.0); // convert from intensity to irradiance at bulb surface

      this.hemiLight.intensity = this.hemiLuminousIrradiances[
        this.params.hemiIrradiance
      ];
      const time = Date.now() * 0.0005;
      const grau = 100;

      this.bulbLight.position.y = Math.cos(grau) * 0.75 + 1.25;

      this.renderer.render(this.scene, this.camera);

      //console.log(this.camera);

      this.stats.update();
    },
    animateThreeJs() {
      this.renderer.render(this.scene, this.camera);
      this.renderer.shadowMap.needsUpdate = true;
    },

    resizeCanvas() {
      this.camera.aspect = window.innerWidth / window.innerHeight;
      this.camera.updateProjectionMatrix();

      this.renderer.setSize(window.innerWidth, window.innerHeight);
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/style/custom.scss";
</style>
