var Vasos = {
  background_color: "#656563",
  buildVaso(p_width, p_height, p_top, p_left) {
    let rect_retangulo = new fabric.Rect({
      width: p_width,
      height: p_height,
      fill: this.background_color,
      originX: "left",
      originY: "top",
      centeredRotation: false,
      selectable: true,
      id: p_width + "x" + p_height,
      name: "clonable",
    });

    let text_top = (45 * rect_retangulo.height) / 100;

    let rect_texto = new fabric.Textbox(rect_retangulo.id, {
      textAlign: "center",
      width: p_width,
      top: text_top,
      fontSize: 10,
      fill: "#fff",
    });

    var g = new fabric.Group([rect_retangulo, rect_texto], {
      left: p_left,
      top: p_top,
    });
    g.hasControls = true;
    g.name = "clonable";
    g.myprops = "vaso";
    return g;
  },
};

export default Vasos;
