import selectionLayout from "../../components/SelectionLayout.vue";
import imageEdition from "../../components/PureJs.vue";
export default {
  name: "Home",
  components: { selectionLayout, imageEdition },
  data() {
    return {
      on_call_link: false,
      value: 1,
      drawer: null,
      reload: false,
      dialog: false,
      detalhes_terreno: false,
      export_canvas: false,
      texture_itens: [
        { text: "Madeira", value: "retina_wood" },
        { text: "Cimento", value: "bedge_grunge" },
        { text: "Grama", value: "grama" },
      ],
      tmp_texture: { text: "Cimento", value: "bedge_grunge" },
      tmp_backgroud_layout: "",
      set_backgroud_details: {
        layout: "form_squad",
        texture: "bedge_grunge",
        width: 10,
        height: 6,
        flipX: true,
      },
      disabled_selected: true,
      remove_obj: false,
      show_context_menu: false,
      show_context_color_picker: false,
      offsetPos: { y: 10, x: 10 },
      tmp_color: "#FF0000",
      swatches: [["#656563"], ["#746757"], ["#44362B"], ["#2F3032"]],
      background_details: {},
    };
  },
  computed: {
    width() {
      return this.set_backgroud_details.width;
    },
    height() {
      return this.set_backgroud_details.height;
    },
  },
  watch: {
    width() {
      this.$nextTick(function() {
        if (this.width > 12) this.set_backgroud_details.width = 12;
        return false;
      });
    },
    height() {
      this.$nextTick(function() {
        if (this.height > 7.5) this.set_backgroud_details.height = 7.5;
        return false;
      });
    },
  },
  created() {
    this.background_details = this.set_backgroud_details;
  },
  methods: {
    showDeveloper() {
      console.log("sistema desenvolvido por fernando.basilio@gmail.com");
      this.dialog = true;
    },
    showDetalhesTerreno: function(p_layout) {
      this.background_details.layout = p_layout;
      this.drawer = false;
      this.detalhes_terreno = true;
    },
    onSetLayout: function() {
      this.detalhes_terreno = false;
      this.set_backgroud_details = this.background_details;
    },
    onReload: function() {
      this.reload = !this.reload;
    },
    onObjectSelected: function(p_selected) {
      this.disabled_selected = p_selected;
    },
    onContextMenu(p_mostra, offsetY, offsetX) {
      this.tmp_color = "#FF0000";
      this.show_context_menu = p_mostra;

      if (this.show_context_menu === false) {
        return true;
      }
      var menuNode = this.$refs.context_menu;
      menuNode.style.top = offsetY + 10 + "px";
      menuNode.style.left = offsetX + 10 + "px";
      this.offsetPos.y = offsetY;
      this.offsetPos.x = offsetX;
    },

    closeContextMenus() {
      this.show_context_menu = false;
      this.show_context_color_picker = false;
    },

    showPicColor() {
      var colorPicker = document.getElementById("color_picker");
      this.tmp_color = "#FF0000";

      //se a chamada teve origem no menu
      if (!this.show_context_menu) {
        colorPicker.style.top = "-5px";
        colorPicker.style.left = "850px";
        this.show_context_color_picker = true;
        return true;
      }

      //se a chamada teve origem no menu de contexto
      this.show_context_menu = false;
      this.onObjectSelected(true);
      this.show_context_color_picker = true;

      // show menu
      colorPicker.style.left = this.offsetPos.x + "px";
      colorPicker.style.top = this.offsetPos.y + "px";
    },
    onRemoveObject() {
      this.closeContextMenus();
      this.remove_obj = !this.remove_obj;
      this.disabled_selected = true;
    },
    onChangeTexture() {
      this.set_backgroud_details.texture = this.tmp_texture;
    },
    onExportObject(p_objCanvas) {
      try {
        localStorage.setItem("obj_canvas", JSON.stringify(p_objCanvas[0]));
        localStorage.setItem(
          "backgroud_details",
          JSON.stringify(p_objCanvas[1])
        );
        localStorage.setItem("svg_obj", JSON.stringify(p_objCanvas[2]));
        this.openPreview();
      } catch (e) {
        console.log("Local Storage is full");
      }
    },
    async openPreview() {
      let routeData = this.$router.resolve({ path: "/view3D" });
      window.open(routeData.href, "_blank");
    },
  },
};
