<template>
  <div style="float:right" id="base">
    <canvas
      ref="plataforma"
      id="plataforma"
      width="1200"
      height="900"
      style="margin-top:10px"
      aspect-ratio="1.7"
      key="plataforma_1"
      v-on:click.left="false"
    ></canvas>
  </div>
</template>

<script>
import { fabric } from "fabric";
import Terrenos from "./Terrenos";
import Vasos from "./Vasos";

export default {
  name: "imageEdition",
  components: {},
  props: [
    "on_call_link",
    "background_details",
    "reload",
    "remove_obj",
    "tmp_color",
    "export_canvas",
  ],
  data() {
    return {
      cPlataforma: null,
      count: 100,
    };
  },

  computed: {
    layout() {
      return this.background_details.layout;
    },
    width() {
      return this.background_details.width;
    },
    height() {
      return this.background_details.height;
    },
    texture() {
      return this.background_details.texture;
    },
    flipX() {
      return this.background_details.flipX;
    },
  },
  watch: {
    on_call_link() {
      this.saveImage();
    },
    remove_obj() {
      this.removeObject();
    },
    layout() {
      this.$nextTick(function() {
        this.addLayout();
      });
    },
    width() {
      this.$nextTick(function() {
        this.addLayout();
      });
    },
    height() {
      this.$nextTick(function() {
        this.addLayout();
      });
    },
    flipX() {
      this.$nextTick(function() {
        this.flipTerreno();
      });
    },
    texture(before, after) {
      this.$nextTick(function() {
        this.setTexture();
      });
    },
    tmp_color(before, after) {
      this.$nextTick(function() {
        this.setColorObject();
      });
    },
    export_canvas() {
      this.$nextTick(function() {
        this.getExportObject();
      });
    },
    reload() {
      this.$nextTick(function() {
        this.cPlataforma = false;
        this.addLayout();
      });
    },
  },
  created() {},
  mounted() {
    this.startImage();
  },
  methods: {
    getExportObject() {
      let that = this;
      var arrObj = this.cPlataforma.getObjects();
      var newArrObj = [];
      var parameter = [];
      var svgObject = [];
      var str_svg =
        '<?xml version="1.0" encoding="UTF-8" standalone="no" ?>\
<!DOCTYPE svg PUBLIC "-//W3C//DTD SVG 1.1//EN" "http://www.w3.org/Graphics/SVG/1.1/DTD/svg11.dtd">\
<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" version="1.1" width="1200" height="900" viewBox="0 0 1200 900" xml:space="preserve">\
<desc>Created with Fabric.js 4.4.0</desc>\
<defs>\
</defs>';
      arrObj.forEach((object, i) => {
        if (object.name == "printable" && object.myprops != "lines") {
          newArrObj.push(object);
          if (object.myprops == "terreno") {
            str_svg = str_svg + object.toSVG() + "</svg>";
            console.log("svg", str_svg);
            svgObject.push(str_svg);
          }
        }
      });
      parameter.push(newArrObj);
      parameter.push(this.background_details);
      parameter.push(svgObject);
      this.$emit("callExportObject", parameter);
    },
    saveImage() {
      var arrObj = this.cPlataforma.getObjects();
      var newArrObj = [];
      arrObj.forEach((object, i) => {
        if (object.name == "printable") newArrObj.push(object);
      });

      let bjBase64 = this.cPlataforma.toDataURL({
        format: "png",
        top: 150,
        quality: 0.8,
      });

      let a = document.createElement("a");
      a.href = bjBase64;
      a.download = "canvas.png";
      a.click();
    },
    addLayout() {
      if (this.cPlataforma) {
        this.setTerreno();
        return true;
      }
      var base = document.getElementById("base");
      base.innerHTML = "";
      let newCanvas = document.createElement("canvas");
      newCanvas.setAttribute(
        "id",
        "plataforma_" + this.background_details.layout
      );
      newCanvas.setAttribute("width", "1200");
      newCanvas.setAttribute("height", "900");
      newCanvas.setAttribute("class", "lower-canvas");
      newCanvas.setAttribute(
        "style",
        "margin-top: 10px; position: absolute; width: 1200px; height: 900px; left: 0px; top: 0px; touch-action: none; user-select: none;"
      );

      base.append(newCanvas);

      this.cPlataforma = new fabric.Canvas(
        "plataforma_" + this.background_details.layout,
        {
          selection: false,
          backgroundColor: null,

          fireRightClick: true,
          fireMiddleClick: true,
          stopContextMenu: false,
        }
      );
      this.startImage();
    },
    setTexture() {
      const self = this.cPlataforma;
      const p_texture = this.background_details.texture;

      self.getObjects().forEach(function(objTexture) {
        if (objTexture.myprops == "terreno") {
          fabric.util.loadImage("assets/" + p_texture + ".png", function(img) {
            objTexture.set(
              "fill",
              new fabric.Pattern({
                source: img,
                repeat: "repeat",
              })
            );

            self.renderAll();
          });
        }
      });
    },
    removeObject() {
      const Obj = this.cPlataforma.getActiveObject();
      this.cPlataforma.remove(Obj);
      this.cPlataforma.renderAll();
    },
    setColorObject() {
      let Obj = this.cPlataforma.getActiveObject();
      let activeObj = Obj._objects[0];
      if (
        this.tmp_color == "#FF0000" ||
        typeof activeObj == "undefined" ||
        typeof activeObj.fill == "undefined" ||
        Obj.myprops !== "vaso"
      )
        return false;
      activeObj.set("fill", this.tmp_color);
      this.cPlataforma.renderAll();
    },
    contextMenu() {
      this.cPlataforma.on("mouse:down", (event) => {
        if (event.button === 1) {
          this.$emit("callContextMenus");
        }
        if (event.button === 2) {
          //console.log("middle click", event);
        }
        if (event.button === 3) {
          const Obj = this.cPlataforma.getActiveObject();
          if (
            typeof Obj == "undefined" ||
            typeof Obj.myprops == "undefined" ||
            Obj.myprops !== "vaso"
          )
            return false;

          // show menu
          this.$emit("callContextMenu", true, event.e.offsetY, event.e.offsetX);
        }
      });
    },
    flipTerreno() {
      const self = this.cPlataforma;

      self.getObjects().forEach(function(objTerreno) {
        if (objTerreno.myprops == "terreno") {
          objTerreno.toggle("flipX");
          self.renderAll();
        }
      });
    },
    setTerreno() {
      const self = this.cPlataforma;
      const proj = this;
      let tmpObj = null;
      self.getObjects().forEach((objTerreno, key) => {
        if (
          typeof objTerreno == "object" &&
          typeof objTerreno.myprops == "string" &&
          objTerreno.myprops == "terreno"
        ) {
          tmpObj = objTerreno;
        }
      });

      let p_width_layout = proj.background_details.width;
      let p_height_layout = proj.background_details.height;
      let p_layout = proj.background_details.layout;

      var tmpTerreno = Terrenos;
      tmpTerreno.setMeasures(p_width_layout, p_height_layout);
      tmpTerreno.setTerreno(p_layout);
      let tmpFormato = tmpTerreno.terreno;

      this.cPlataforma.remove(tmpObj);
      this.cPlataforma.renderAll();
      this.cPlataforma.add(tmpFormato);
      this.cPlataforma.renderAll();
      tmpFormato.moveTo(0);

      this.cPlataforma.renderAll();
      proj.setTexture();
    },
    startImage() {
      if (!this.cPlataforma)
        this.cPlataforma = new fabric.Canvas("plataforma", {
          selection: false,
          backgroundColor: null,

          fireRightClick: true,
          fireMiddleClick: true,
          stopContextMenu: false,
        });

      this.contextMenu();
      var grid = 25;
      let p_width_layout = this.background_details.width;
      let p_height_layout = this.background_details.height;
      let p_layout = this.background_details.layout;

      /*let terreno = Terrenos.getTerreno(
        p_width_layout,
        p_height_layout,
        p_layout
      );*/

      var tmpTerreno = Terrenos;
      tmpTerreno.setMeasures(p_width_layout, p_height_layout);
      tmpTerreno.setTerreno(p_layout);

      var terreno = tmpTerreno.terreno;

      this.cPlataforma.add(terreno);
      terreno.set("flipX", true);
      this.setTexture();

      //grid
      //quantidade linhas paralelas vertical
      for (var i = 0; i < 1250 / grid; i++) {
        //altura linhas verticais
        this.cPlataforma.add(
          new fabric.Line([i * grid, 150, i * grid, 900], {
            stroke: "#ccc",
            selectable: false,
            name: "printable",
            myprops: "lines",
          })
        );
      }
      //Container dos vasos
      //quantidade linhas paralelas horizontais
      for (var i = 6; i < 900 / grid; i++) {
        this.cPlataforma.add(
          //largura linhas horizontais
          new fabric.Line([0, i * grid, 1200, i * grid], {
            stroke: "#ccc",
            selectable: false,
            name: "printable",
            myprops: "lines",
          })
        );
      }
      this.addVasos();

      this.cPlataforma.add(
        new fabric.Line([400, 0, 400, 150], {
          stroke: "#ccc",
          selectable: false,
          myprops: "lines",
        })
      );
      this.cPlataforma.add(
        new fabric.Line([0, 150, 1200, 150], {
          stroke: "#ccc",
          selectable: false,
          name: "printable",
          myprops: "lines",
        })
      );

      const self = this.cPlataforma;
      const selfRoot = this;
      this.cPlataforma.on({
        "mouse:down": function(e) {
          if (e.target && e.target.myprops == "vaso") {
            selfRoot.$emit("callObjectSelected", false);
          }
          if (e.target && e.target.name == "clonable") {
            e.target.clone(function(clonedObj) {
              clonedObj.set({
                left: e.target.left,
                top: e.target.top,
                evented: true,
                name: "clonable",
                selectable: true,
                myprops: "vaso",
              });
              clonedObj.hasControls = true;
              e.target.centeredRotation = false;
              e.target.originX = "center";
              e.target.originY = "center";
              e.target.hasControls = true;
              e.target.transparentCorners = false;
              e.target.setControlsVisibility({
                tl: false,
                bl: false,
                tr: false,
                br: false,
                ml: false,
                mt: false,
                mr: false,
                mb: false,
                mtr: true,
              });
              e.target.name = "printable";

              if (clonedObj.type === "activeSelection") {
                clonedObj.canvas = self;
                clonedObj.forEachObject(function(obj) {});
                // this should solve the unselectability
                clonedObj.setCoords();
              } else {
                self.add(clonedObj);
              }
            });
          }
        },
        "mouse:up": function(e) {
          self.requestRenderAll();
        },
      });
    },
    addVasos() {
      var top_vasos = 5;

      //50x50
      let objVaso = {};
      objVaso = Vasos.buildVaso(50, 50, top_vasos, 5);
      this.cPlataforma.add(objVaso);
      objVaso = Vasos.buildVaso(60, 60, top_vasos, 75);
      this.cPlataforma.add(objVaso);
      objVaso = Vasos.buildVaso(80, 80, top_vasos, 160);
      this.cPlataforma.add(objVaso);
      objVaso = Vasos.buildVaso(100, 100, top_vasos, 270);
      this.cPlataforma.add(objVaso);
      /**outra coluna */
      objVaso = Vasos.buildVaso(100, 60, top_vasos, 420);
      this.cPlataforma.add(objVaso);
      objVaso = Vasos.buildVaso(125, 60, top_vasos, 545);
      this.cPlataforma.add(objVaso);
      objVaso = Vasos.buildVaso(150, 60, top_vasos, 695);
      this.cPlataforma.add(objVaso);
      objVaso = Vasos.buildVaso(200, 60, top_vasos, 870);
      this.cPlataforma.add(objVaso);
      this.cPlataforma.renderAll();
    },
  },
};
</script>

<style>
html,
body {
  height: 100%;
  font-family: "Roboto";
}
canvas {
  border: 1px solid #ccc;
}
</style>
