var Terrenos = {
  terreno: "",
  block_height: 10,
  block_width: 10,
  block_top_height: 10,
  block_base_witdh: 10,
  block_midle_height: 10,
  block_base_height: 10,

  setMeasures(p_width_layout, p_height_layout) {
    var proporcao = 100;
    this.block_height = p_height_layout * proporcao;
    this.block_width = p_width_layout * proporcao;

    //Elle size
    this.block_top_height = Math.ceil(p_height_layout * 0.5333) * proporcao; //53.33% to height
    this.block_base_witdh = Math.ceil(p_width_layout * 0.375) * proporcao; //37,5% to height

    //Uh size
    this.block_midle_height = Math.ceil(p_height_layout * 0.3333) * proporcao; //33.33% to height
    this.block_base_height = Math.ceil(p_height_layout * 0.6666) * proporcao; //66.66% to height
  },
  setTerreno(p_layout) {
    //squad
    var shape_form_squad = new fabric.Rect({
      name: "printable",
      myprops: "terreno",
      fill: "#ddd",
      left: 0,
      top: 150,
      centeredRotation: false,
      selectable: false,
      width: this.block_width,
      height: this.block_height,
    });

    //elle
    var points = [
      {
        x: 0,
        y: 0,
      },
      {
        x: this.block_width,
        y: 0,
      },
      {
        x: this.block_width,
        y: this.block_top_height, //400 -> 275
      },
      {
        x: this.block_base_witdh, //500 -> 375
        y: this.block_top_height, //400 -> 275
      },
      {
        x: this.block_base_witdh, //500
        y: this.block_height,
      },
      {
        x: 0,
        y: this.block_height,
      },
    ];
    var shape_form_elle = new fabric.Polygon(points, {
      id: "elle",
      name: "printable",
      myprops: "terreno",
      fill: "#333",
      left: 0,
      top: 150,
      strokeWidth: 0,
      transparentCorners: true,
      centeredRotation: false,
      selectable: false,
    });

    //uh
    var points = [
      {
        //0
        x: 0,
        y: 0,
      },
      {
        //1
        x: this.block_width,
        y: 0,
      },
      {
        //2
        x: this.block_width,
        y: this.block_midle_height,
      },
      {
        //3
        x: this.block_base_witdh,
        y: this.block_midle_height,
      },
      {
        //4
        x: this.block_base_witdh,
        y: this.block_base_height,
      },
      {
        //5
        x: this.block_width,
        y: this.block_base_height,
      },
      {
        //6
        x: this.block_width,
        y: this.block_height,
      },
      {
        //7
        x: 0,
        y: this.block_height,
      },
    ];

    var shape_form_uh = new fabric.Polygon(points, {
      id: "uh",
      name: "printable",
      myprops: "terreno",
      fill: "#333",
      left: 0,
      top: 150,
      strokeWidth: 0,
      transparentCorners: true,
      centeredRotation: false,
      selectable: false,
    });

    // Define the Data
    switch (p_layout) {
      case "form_squad":
        this.terreno = shape_form_squad;
        break;
      case "form_elle":
        this.terreno = shape_form_elle;
        break;
      case "form_uh":
        this.terreno = shape_form_uh;
        break;
    }
  },
};

export default Terrenos;
